.navVar {
    position: -webkit-sticky;
    position: sticky; 
    top: 0;
    width: 100%;
    background-color: #2c3e50;    
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    text-transform: uppercase;
    font-family: Montserrat,"Helvetica Neue",Helvetica,Arial,sans-serif;
    z-index: 9;
}

.navVar h1 {
    padding: 10px;
    color: white;
}

.navVar ul {
    display: flex;
    flex-direction: row;
    color: white;
    list-style: none;
    font-size: 1.2rem;
    font-weight: bold;
}

.hamburger {
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: white;
    cursor: pointer;
    display: none;
}

.hamburger:focus {
    outline: none;
}

.navVar h1:hover, li:hover {
    cursor: pointer;
}

.navVar ul a {
    margin-left: 20px;
    border-bottom: 3px solid transparent;
}

.navVar ul a:hover, .navVar ul a .active {
    padding-bottom: 6px;
    border-bottom: 3px solid #18bc9c;
    margin-bottom: -5px;
}

@media screen and (max-width: 767px) {

    .navVar ul {
        display: none;      
        flex-direction: column;   
        width: 100%;
        margin: 0 20px 20px auto;
        text-align: right;
    }

    .navVar ul.show {
        display: flex;
    }

    .navVar li {
        margin: 5px 0 5px 0;
    }
    
    .hamburger {
        display: block;
    }
}

.presentation {
